<template>
  <div
    ref="girlsDetails"
    class="girlsDetails-content">
    <div
      class="girlsDetails-back-box"
      :class="{ backActive: navShow }">
      <div
        class="girlsDetails-back"
        @click="backFun">
        <img src="../../assets/images/bsleft_icon.png" />
      </div>
      {{ navTitle }}
    </div>
    <img
      class="girlsDetails-head-img"
      src="../../assets/images/nyhead.png" />
    <div class="girlsDetails-cell">
      <div class="girlsDetails-head-content">
        <div class="girlsDetails-head-box" >
          <div class="girlsDetails-head-container">
            <base-image
              class="girlsDetails-head"
              :url="details.imagebase64.url"
              :type="details.imagebase64.type"
              encrypt
              round/>
          </div>
          <p class="girlsDetails-head-name">{{ total }}部影片</p>
        </div>
        <div class="girlsDetails-cell">
          <span class="girlsDetails-name">{{ details.artist }}</span>
          <span class="girlsDetails-sr-font">{{ details.birthday }}</span>
        </div>
      </div>
    </div>

    <div class="girlsDetails-cntent-box">
      <div class="girlsDetails-cell">
        <span class="girlsDetails-sr-font">身高：{{ details.height }}cm</span>
        <span
          class="girlsDetails-sr-font">三圍：{{ details.measurement }}cm</span>
        <span class="girlsDetails-sr-font">{{ details.cup }}杯</span>
      </div>
      <div class="girlsDetails-xx-content">
        <div class="title">簡介</div>
        <p :class="{ avtive: gdIsShow }">{{ details.desc }}</p>
        <div
          class="girlsDetails-xx-content-item"
          @click="gdfun()">
          更多
          <img
            src="../../assets/images/hsright_iocn.png"
            alt />
        </div>
      </div>
    </div>
    <div class="girlsDetails-view-box">
      <div class="girlsDetails-title">相關影集</div>
      <ul class="girlsDetails-view">
        <li
          v-for="(item, index) in videoList"
          :key="index"
          class="girlsDetails-view-cell"
          @click="itemChange(item)">
          <base-image
            class="girlsDetails-view-img"
            :url="item.coverbase64.url"
            :type="item.coverbase64.type"
            encrypt/>
          <p class="girlsDetails-video-title">{{ item.title }}</p>
        </li>
      </ul>
      <van-loading
        v-if="loading"
        size="24px">加载中...</van-loading>
    </div>
  </div>
</template>
<script>
import { debounce } from '@/utils/index'
import baseImage from '@/components/baseImage'

export default {
  components: {
    baseImage
  },
  data () {
    return {
      videoList: '',
      allNum: 0,
      gdIsShow: true,
      navShow: false,
      navTitle: '',
      details: {
        artist: '-',
        birthday: '1990-08-14 00:00:00',
        country: '-',
        cup: '-',
        desc: '-',
        height: 163,
        id: 1,
        imagebase64: {},
        image: '',
        measurement: ''
      },
      page: 1,
      loading: false,
      total: 0
    }
  },
  async mounted () {
    window.addEventListener('scroll', this._handleScroll)
    await this.getVideoList()
  },
  created () {
    this._handleScroll = debounce(this.handleScroll, 500)
  },
  beforeDestroy () {
    window.removeEventListener('scroll', this._handleScroll)
  },
  methods: {
    onScrollGirlsDetails () {
      console.log(
        this.$refs.girlsDetails.scrollTop,
        this.$refs.girlsDetails.scrollHeight
      )
    },
    handleScroll () {
      // 頁面滾動距頂部距離
      const scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop
      const scrollHeight = document.documentElement.scrollHeight
      const offsetHeight = document.documentElement.offsetHeight
      const scroll = scrollTop - this.i
      this.i = scrollTop
      console.log('scrollTop', scrollTop, scrollHeight, offsetHeight)
      if (scrollTop + offsetHeight > scrollHeight - offsetHeight * 0.2) {
        this.getVideoList()
      }
      if (scroll < 0) {
        if (scrollTop <= 44) {
          this.navShow = false
          this.navTitle = ''
        }
      } else {
        if (scrollTop >= 44) {
          this.navShow = true
          this.navTitle = this.details.artist
        }
      }
    },
    async getVideoList () {
      if (this.loading) {
        return
      }
      if (this.total !== 0 && this.total <= this.videoList.length) {
        return
      }
      this.loading = true
      const res = await this.request({
        url:
          '/api/artistDetail?artistId=' +
          this.$route.query.artistId +
          '&page=' +
          this.page
      })
      if (res.rescont) {
        this.videoList =
          this.page === 1
            ? res.rescont.videoList.data
            : this.videoList.concat(res.rescont.videoList.data)
        this.total = res.rescont.videoList.total
        this.details = res.rescont
        this.page = this.page + 1
      }
      this.loading = false
    },
    itemChange (item) {
      try {
        this.$router.push({
          path: '/playVideo',
          query: {
            id: item.id
          }
        })
      } catch (error) {}
    },
    // 更多點擊方法
    gdfun () {
      if (this.gdIsShow) {
        this.gdIsShow = false
      } else {
        this.gdIsShow = true
      }
    },
    // 返回點擊方法
    backFun () {
      this.$router.back()
    }
  }
}
</script>
<style scoped>
.girlsDetails-content {
  position: relative;
  width: 100%;
  margin-bottom: 55px;
  overflow-x: hidden;
  background-color: #161823;
}

.girlsDetails-cntent-box {
  position: relative;
  width: 100%;
}

.girlsDetails-head-img {
  width: 100%;
  vertical-align: bottom;
}

.girlsDetails-head-content {
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}

.girlsDetails-back-box {
  position: fixed;
  right: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 44px;
  font-size: 18px;
  line-height: 44px;
  text-align: center;
}

.backActive {
  color: #fff;
  background-color: #161823;
}

.girlsDetails-back {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  box-sizing: border-box;
  width: 44px;
  height: 44px;
  padding: 13px 15px;
}

.girlsDetails-back img {
  width: 10px;
  height: 18px;
  vertical-align: top;
}

.girlsDetails-title {
  width: 100%;
  padding-left: 15px;
  font-family: PingFang SC;
  font-size: 15px;
  font-weight: 500;
  line-height: 30px;
  color: rgba(255, 255, 255, 1);
  text-align: left;
}

.girlsDetails-head-box {
  position: relative;
  width: 100%;
  margin-bottom: 10px;
  text-align: center;
}

.girlsDetails-head-container {
  display: inline-block;
  width: 80px;
  height: 80px;
  border: 2px solid rgba(39, 43, 65, 1);
  border-radius: 50%;
}

.girlsDetails-head {
  display: inline-block;
  width: 80px;
  height: 80px;
  vertical-align: bottom;
}

.girlsDetails-head-name {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 80px;
  height: 20px;
  margin: 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
  color: rgba(255, 255, 255, 1);
  background:
    -webkit-linear-gradient(
      left,
      #ff3574,
      #ff004f
    ); /* Safari 5.1 - 6.0 */

  background:
    -o-linear-gradient(
      left,
      #ff3574,
      #ff004f
    ); /* Opera 11.1 - 12.0 */

  background:
    -moz-linear-gradient(
      left,
      #ff3574,
      #ff004f
    ); /* Firefox 3.6 - 15 */

  background: linear-gradient(to left, #ff3574, #ff004f); /* 標準的語法 */
  border-radius: 10px;
  transform: translateX(-50%);
}

.girlsDetails-cell {
  position: relative;
  width: 100%;
  text-align: center;
}

.girlsDetails-name {
  font-family: PingFang SC;
  font-size: 18px;
  font-weight: 500;
  line-height: 18px;
  color: rgba(255, 255, 255, 1);
}

.girlsDetails-sr-font {
  margin-left: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 30px;
  color: rgba(211, 215, 226, 1);
}

.girlsDetails-xx-content {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 0 15px 10px 15px;
  text-align: left;
}

.girlsDetails-xx-content .title {
  position: relative;
  width: 100%;
  font-size: 15px;
  font-weight: 500;
  line-height: 30px;
  color: rgba(255, 255, 255, 1);
}

.girlsDetails-xx-content p {
  width: 100%;
  padding: 0 0;
  margin: 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px;
  color: rgba(154, 157, 164, 1);
}

.girlsDetails-xx-content .avtive {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.girlsDetails-xx-content-item {
  position: absolute;
  right: 0;
  bottom: -7px;
  width: 60px;
  padding: 5px 0;
  font-size: 13px;
  font-weight: 500;
  line-height: 13px;
  color: rgba(250, 206, 21, 1);
}

.girlsDetails-xx-content-item img {
  width: 5px;
  height: 10px;
  margin-left: 5px;
  vertical-align: inherit;
}

.girlsDetails-view {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  padding: 0 0;
  margin: 0 0;
  list-style: none;
}

.girlsDetails-view-box {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  padding: 20px 0 10px 0;
  border-bottom: 5px solid #0e121b;
}

.girlsDetails-view-cell {
  position: relative;
  box-sizing: border-box;
  width: 48.5%;
  padding: 10px 0 0 0;
}

.girlsDetails-view-img {
  position: relative;
  width: 100%;
  height: 100px;
  overflow: hidden;
  background-color: #161823;
}

.girlsDetails-view-img img {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  width: 100%;
  min-height: 100%;
  transform: translateY(-50%);
}

.girlsDetails-video-date {
  position: absolute;
  right: 5px;
  bottom: 35px;
  font-size: 11px;
  font-weight: 500;
  line-height: 20px;
  color: rgba(255, 255, 255, 1);
  text-align: right;
  letter-spacing: 1.2px;
}

.girlsDetails-video-title {
  box-sizing: border-box;
  width: 100%;
  padding: 0 8px;
  margin: 0 0;
  overflow: hidden;
  font-size: 14px;
  font-weight: 500;
  line-height: 30px;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  text-overflow: ellipsis;
  letter-spacing: 1.5px;
  white-space: nowrap;
}
</style>



